* {
  font-family: "Roboto", sans-serif;
  line-height: 1.4;
}
.border-lines {
  border-image-slice: 100;
  border: 4px solid transparent;
  border-image: url(./assets//images//black-line.png) 20 round;
}

.border-top-line {
  border-image-slice: 100;
  border-top: 4px solid transparent;
  border-image: url(./assets//images//black-line.png) 20 round;
}

.btn-custom {
  border: solid 2px #707070 !important;
  background-color: transparent !important;
  width: 100px !important;
  &:hover {
    color: white !important;
    background-color: #707070 !important;
  }
}

.hamburger-icon {
  font-size: 50px;
  color: #707070;
}

.dropdown-menu-custom {
  background-color: rgba(255, 255, 255, 0.7) !important;
  transition: all 600ms !important;
  animation-name: example;
  animation-duration: 650ms;
  border: none !important;
  width: 300px;
  padding: 0 !important;

  @media only screen and (max-width: 600px) {
    width: 200px;
  }
}

.dropdown-item-custom {
  font-size: 20px !important;
  color: #6d6e70 !important;
  transition:all .3s;
  text-transform: uppercase !important;
  padding: 0.45rem 1rem !important;
  &:hover {
    background-color: #6d6e70 !important;
    color: #ffffff !important;
  }
}

.dropdown-menu-custom.show {
  @keyframes example {
    from {
      opacity: 0;
    }
    to {
      opacity: 1 !important;
    }
  }
}

.footer-main {
  background-color: #6d6e70;
  //   background-color: white;
}
.dotted-border {
  border: 5px solid transparent;
  border-image: url(./assets//images//black-line.png) 10 round;
}

.footer-border {
  border: 5px solid transparent;
  padding: 15px 15px 10px 15px;

  border-image: url(./assets//images/white-line.png) 10 round;
}

.footer-border-top {
  border-top: 5px solid transparent;
  padding: 15px;
  border-image: url(./assets//images/white-line.png) 10 round;
}
.social-icon {
  width: auto;
  height: 25px;
  margin-right: 20px;
  margin-bottom: 10px;
}

.footer-sub-heading {
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
}

.standard-icon {
  width: auto;
  height: 22px;
}

.footer-normal-text {
  line-height: 1.3;
}

.footer-links {
  padding: 0px;
  margin: 0px;
  font-weight: 500;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  list-style: none;
}

.footer-link {
  margin-bottom: 15px;
  a {
    margin-bottom: 15px;
    text-decoration: none;
    color: #ffffff;
    &:hover {
      color: #ffffff;
    }
  }
}

.hero-section {
  position: relative;
}

.hero-relative-section {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.hero-main-text {
  font-size: 32px;
  font-weight: 300;
  color: #ffffff;
  margin: 15px 0 0 0;
  @media only screen and (max-width: 768px) {
    font-size: 18px;
  }
}

.hero-image {
  width: 100%;
  height: auto;
}
.hero-inner-image {
  width: 100%;
  height: auto;
  max-width: 525px;
}

.home-main-heading {
  margin: 0;
  color: #6d6e70;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 2.2rem;
  @media only screen and (max-width: 600px) {
    font-size: 1.7rem;
  }
}

.text-small {
  font-size: small;
  font-weight: 300;
  color: #6d6e70;
  margin: 0;
}

.text-normal {
  font-size: 14px;
  font-weight: 400;
  color: #6d6e70;
}

.text-semi-lg {
  font-size: 18px;
  font-weight: 400;
  color: #6d6e70;
  margin: 0;
}

.signup-back {
  background-image: url("../src/assets/images/wood-back.png");
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 400px; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  transition: all 300ms;
  display: block;
  text-decoration: none;
  &:hover {
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  }
}

.signup-back-full {
  background-image: url("../src/assets/images/wood-full.png");
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 400px; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  transition: all 300ms;
  display: block;
  text-decoration: none;
  &:hover {
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  }
}

.signup-back-text {
  margin: 0;
  margin-bottom: 20px;
  color: #ffffff;
  font-weight: 500;
  letter-spacing: 2px;
  line-height: 1;
  text-align: center;
  @media only screen and (max-width: 320px) {
    font-size: 24px;
  }
}


.font-weight-normal {
  font-weight: 400;
}

.font-weight-high {
  font-weight: 500;
}

.font-extra-small {
  font-size: 0.775rem;
}

.accordion-button {
  border-image-slice: 100 !important;
  border-top: 4px solid transparent !important;
  border-image: url(./assets//images//black-line.png) 20 round !important;
  color: #6d6e70 !important;
  font-size: 20px !important;
}
.accordion-button::after{
  content:none !important;
}
.accordion-item {
  border: none !important;
}

.accordion-button:not(.collapsed) {
  box-shadow: none !important;
  background-color: white !important;
  color: #6d6e70 !important;
}
.accordion-button:focus {
  box-shadow: none !important;
}
.accordion-body {
  padding: 0 1.25rem !important;
}

.custom-form-label {
  font-size: 22px;
  text-transform: uppercase;
  color: #6d6e70;
  font-weight: 300;
}

.text-default {
  color: #6d6e70;
}

.form-control-lg {
  min-height: calc(1.7em + 1rem + 2px) !important;
}

.form-control:focus {
  border-color: transparent !important;
  box-shadow: 0 0 0 0.25rem rgba(109, 109, 111 , 0.25) !important;
}

.send-button {
  border: 4px solid red;
  box-shadow: none;
  font-size: 35px;
  color: red;
  text-transform: uppercase;
  background-color: white;
  font-weight: 900;
  font-style: italic;
  padding: 0px 20px;
  min-width: 220px;

  &:hover {
    background-color: red;
    color: white;
  }
}

.button-tab-default {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  display: block;
  width: 100%;
  background-color: #6d6e70;
  color: #ffffff;
  padding: 8px 5px;
  margin-bottom: 10px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  font-size: 28px;
  border: 3px solid #6d6e70;
  font-weight: 500;
  @media only screen and (max-width: 600px) {
    font-size: 20px;
  }
  &:hover {
    background-color: white;
    color: #fd0d1b;
    border: 3px solid #fd0d1b;
  }
  &.button-tab-selected {
    color: #ffffff;
  }
}

.button-tab-selected {
  background-color: #fd0d1b !important;
  color: #ffffff;
  border: 3px solid #fd0d1b;
}

.checkbox-block {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.checkbox-effect:not(:checked),
.checkbox-effect:checked {
  position: absolute;
  left: -9999px;
}

label {
  cursor: pointer;
  position: relative;
  display: flex !important;
  align-items: center;
  font-size: 20px;
  color: #707070;
  font-weight: 300;
  text-transform: uppercase;
}

//vars
$accent-font-color: #db324d;
$border-color: #5a5a5a;

$icon-color-1: #fff;
$icon-color-2: #00711b;
$icon-color-3: #fd5200;
$icon-color-4: #72195a;
$icon-color-5: #c42021;

$icon-bg-1: #1b998b;
$icon-bg-2: #275dad;

//position absolute mixin
@mixin position-absolute($top: null, $left: null, $right: null, $bottom: null) {
  position: absolute;
  top: $top;
  left: $left;
  right: $right;
  bottom: $bottom;
}

.checkbox-custom + label {
  overflow: hidden;
  user-select: none;
}

.checkbox-custom:not(:checked) + label:before,
.checkbox-custom:checked + label:before {
  content: "";
  display: inline-block;
  min-width: 50px;
  min-height: 50px;
  border: 4px solid #fd0d1b;
  box-sizing: border-box;
  margin-right: 20px;
}

.checkbox-custom:not(:checked) + label:after,
.checkbox-custom:checked + label:after {
  content: "✓";
  color: $accent-font-color;
  font-weight: 900;
  font-size: 40px;
  display: flex;
  height: 100%;
  align-items: center;
  @include position-absolute($left: 10px, $top: 0);
  color: $icon-color-3;
}

.checkbox-custom:not(:checked) + label:after {
  left: -52px;
}

.checkbox-custom:checked + label:after {
  left: 10px;
}



#video {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}

#video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}